.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.bbb {
  margin: 5vh auto 0vw auto;
  line-height: 33px;
  font-size: calc(16spx + 2vmin);
  font-family: sans-serif;
  color: #fff;
  background-color: #282c34;
  border-radius: 25px;
  width: 70vw;
}
#headmy {
  margin-top: 10vh;
  color: #fff;
  
}
#headmy p{
	margin: 0 25vw 0 25vw;
  text-align: center;
  font-size: calc(28px + 2vmin);
  color: #c9aa5d;
}
#headmy img {
  filter: invert(.5);
  display: block;
  margin: 20px auto 10px auto;
}
#freepik {
  font-size: calc(2px + 2vmin);
}
.bck {
  margin-top: 0vh;
  object-fit: cover;
  width: 99vw;
  height: 100vh;
  position: relative;
  z-index: 0;
}
.bck-overlay {
  position: absolute;
  margin: auto;
  width: 50%;
  z-index: 1
}
.video-my {
  object-fit: cover;
  width: 99vw;
  height: 100vh;
  position: relative;
  z-index: 0;
}
.video-overlay {
  position: absolute;
  margin: auto;
  width: 50%;
  z-index: 1
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

